.student_top .head_main .head_in {
	padding: 23px 0;
	background: transparent;
}

.student_top .head_main .head_in .head_lft .head_lft_first li a {
	min-width: 42px;
	text-align: center;
	margin: 0px !important;
}

.student_top .head_main .head_in .head_lft .head_lft_first li:first-child a {
	padding: inherit;
}

.student_top .head_main .head_in .head_lft .head_lft_first li:first-child {
	margin-left: 11px !important;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown {
	background-color: #fff;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	position: absolute;
	left: 50%;
	top: 50px;
	min-width: 290px;
	transform: translateX(-50%);
	padding: 5px 0 3px 0;
	display: none;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li {
	width: 100%;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li a {
	background-color: transparent;
	box-shadow: none;
	display: flex;
	align-items: center;
	text-align: right;
	border-radius: 0;
	height: auto;
	padding: 12px 15px 7px 15px;
	justify-content: flex-start;
	width: 100%;
	border-bottom: solid 1px rgba(209,215,228,0.5);
	margin: 0;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li a * {
	line-height: normal;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li a figure {
	border-radius: 6px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	margin-left: 10px;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li a h5 {
	font-size: 14px;
	font-weight: 400;
	line-height: 0.7;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li a span {
	font-size: 12px;
	opacity: 0.8;
	font-weight: 400;
	margin-right: 0;
}

.student_top .head_main .head_in .head_lft .head_lft_first .notification_main .sub-dropdown li:last-child a {
	border: 0;
}

.student_top .head_main .head_in .head_lft .user li a {
	background-color: #e5f6ff;
	margin-right: 11px;
}

.student_top .head_main .head_in .head_lft .user li a span {
	padding: 0 10px 0 32px;
}

.student_top .head_main.fixed_header {
	background-color: transparent;
	box-shadow: none;
}

.student_top .top_img {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: top center;
	height: 172px;
	width: 100%;
	z-index: -1;
}

.student_top .count {
	background-color: #de4682;
	right: -6px;
	top: -3px;
}

.modal-content {
	overflow: hidden;
}

.mimsha_body .txt-blue {
	color: #0af;
}

.blue_btn.txt-blue {
	color: #0af;
}

.student_normal .main {
